.card{
    position: relative;
}

.card img{
    width: 80%;
    display: block;
    border: 2px solid #fff;
    border-radius: 6px;
}

/* front of card - the picture */
.card .front{
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}

.flipped .front{
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}

/* back of card - cover */
.card .back{
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
}

.flipped .back{
    transform: rotateY(90deg);
    transition-delay: 0s;
}

/* mobile */
@media only screen and (max-width: 768px){
    .card img{
        width: 100%;
    }
}

/* tablet */
@media only screen and (max-width: 1080px){
    .card img{
        width: 80%;
        margin-left: 12px;
    }
}